import "semantic-ui-css/semantic.min.css";
import React, { Component } from "react";
import { Button, Form, Container, Header } from "semantic-ui-react";
import axios from "axios";
import { BasicSearch } from "./Table";
import "../styles.css";

export default class Dashboard extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: "",
      taluka: "",
      mobile: "",
      district: "",
      village: "",
      designation: "",
      showRegistrationForm: false,
      showTable: false,
      isAlert: false
    };
  }

  changeHandler = (e) => {
    this.setState({ [e.target.name]: e.target.value });
  };

  submitHandler = (e) => {
    e.preventDefault();
    console.log(this.state);
    this.setState({
      isAlert: true
    });
    axios
      .post("https://jain-minority-people.herokuapp.com/addRow", this.state)
      .then((response) => {
        this.resetData();
        setInterval(() => {
          this.setState({
            isAlert: false
          });
        }, 1000);
        console.log(response);
      });
  };

  resetData() {
    this.setState({
      name: "",
      taluka: "",
      mobile: "",
      district: "",
      village: "",
      designation: ""
    });
  }

  displayHomePage() {
    return (
      <div className="manPage">
        <Button
          color="blue"
          onClick={() => {
            this.setState({
              showTable: true
            });
          }}
          type="submit"
        >
          See records
        </Button>
        <Button
          onClick={() => {
            this.setState({
              showRegistrationForm: true
            });
          }}
          color="blue"
          type="submit"
        >
          Member Registration
        </Button>
      </div>
    );
  }

  render() {
    const {
      showRegistrationForm,
      name,
      taluka,
      village,
      mobile,
      district,
      designation,
      isAlert,
      showTable
    } = this.state;
    if (!showRegistrationForm && !showTable) {
      return <div className="mainPageContainer">{this.displayHomePage()}</div>;
    }
    if (showTable) {
      return (
        <>
          <button
            onClick={() => {
              this.setState({
                showTable: false
              });
            }}
          >
            {"< = back"}
          </button>
          <BasicSearch />
        </>
      );
    }
    return (
      <>
        <button
          onClick={() => {
            this.setState({
              showRegistrationForm: false
            });
          }}
        >
          {"<= back"}
        </button>
        <Container fluid className="container">
          <Header as="h2">Jain Minority Member Registration Form</Header>
          {isAlert && (
            <div className="alert">
              {
                "Record has been saved successfully  you can check on Records..."
              }
            </div>
          )}
          <Form className="form" onSubmit={this.submitHandler}>
            <Form.Field>
              <label>Name</label>
              <input
                placeholder="Enter your name"
                type="text"
                name="name"
                value={name}
                onChange={this.changeHandler}
              />
            </Form.Field>
            <Form.Field>
              <label>Designation</label>
              <input
                placeholder="Enter your designation"
                type="text"
                name="designation"
                value={designation}
                onChange={this.changeHandler}
              />
            </Form.Field>
            <Form.Field>
              <label>Village</label>
              <input
                placeholder="Enter your village"
                type="text"
                name="village"
                value={village}
                onChange={this.changeHandler}
              />
            </Form.Field>
            <Form.Field>
              <label>Taluka</label>
              <input
                placeholder="Enter your taluka"
                name="taluka"
                value={taluka}
                onChange={this.changeHandler}
              />
            </Form.Field>
            <Form.Field>
              <label>Mobile</label>
              <input
                placeholder="Enter your mobile"
                name="mobile"
                value={mobile}
                onChange={this.changeHandler}
              />
            </Form.Field>
            <Form.Field>
              <label>District</label>
              <input
                placeholder="Enter your district"
                type="text"
                name="district"
                value={district}
                onChange={this.changeHandler}
              />
            </Form.Field>

            <Button color="blue" type="submit">
              Submit
            </Button>
          </Form>
        </Container>
      </>
    );
  }
}
